import { mapState, mapActions, mapGetters , mapMutations } from 'vuex'
export default {
	data() {
		return {
			selectData:[],
			totalprice:0
		}
	},
	computed:{
		...mapState('shopcar',['orderLines','orderLines2']),
		...mapGetters('product',['productFilter']),
		...mapGetters('shopcar',['total','total2'])
	}, 
	mounted() { 
		// this.searchShopData()
	},
	methods: {
		...mapMutations('shopcar',['TobuyShop']),
		checkboxChange(e){
			let arr = Array.from(this.orderLines.values())
			this.selectData = e.detail.value
			let result = 0
			// 先把选中的数据带到确认订单
			if(e.detail.value.length!==0){
				let result = 0
				for( let i=0; i<e.detail.value.length; i++) {
					const item = arr[i]
					if(e.detail.value.includes(item.productName)){
						let orderLine2 = {
							productId:item.productId,  //id
							productName:item.productName,
							price:item.price,
							number:item.number,
							photo:item.photo
						} 	
						this.TobuyShop(orderLine2)
					}
				} 
			}
			// 对所选取的商品的价格进行计算
			if(e.detail.value.length!==0){
				e.detail.value.forEach((item)=>{
					let newarr = arr.filter((item2)=>{
						return item2.productName == item
					})
					for(let i=0;i<newarr.length;i++){
						result += newarr[i].number * newarr[i].price
					}
					this.totalprice = result
				})
			}else{
				this.totalprice = 0
			}
		},
		// 跳转下单页面
		ToComputePrice(){
			uni.navigateTo({
				url:'../order/order?price='+this.totalprice
			})
		}
	}
}
